/* styling related to the posts */
.forum {
  margin-top: 30px;
}

.forum .num {
  font-size: 30px;
  font-weight: 600;
  margin: 0px auto;
  margin-bottom: -10px;
  display: block;
  padding-left: 6px;
}

.forum .num-text {
  text-transform: uppercase;
  font-size: 10px;
  position: relative;
  margin-top: 20px;
}

div .forum-stats {
  vertical-align: top;
  display: inline-block;
  text-align: center;
}

.divider {
  position: absolute;
  left: 0%;
  top: 10%;
  bottom: 10%;
  border-left: 1px solid black;
}

.post-preview {
  margin-top: 20px;
  margin-bottom: 0px;
  font-size: 17px;
}

.post-preview-date {
  margin-top: -5px;
  font-size: 12px;
}

.topic-container {
  padding-left: 7px;
}

.topic-info {
  margin: 0;
  padding: 0;
  padding-top: 18px;
}

h4 {
  margin: 0px;
  margin-top: 25px;
}

.button-container {
  display: flex;
  padding-bottom: 10px;
}
.forum-buttom {
  margin-left: auto;
  padding: 0px 10px 0px 10px;
}

.tag {
  display: inline-flex;
  column-gap: 10px;
}

.topic-button {
  display: inline-flex;
  column-gap: 10px;
  float: right;
}

.editor-placeholder {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
}

.place-holder {
  padding-bottom: 20px;
}

.reply-post {
  margin-left: 90px;
}
