/* Card Style */
.headshot-img {
  border-radius: 50%;
  box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
  display: inline-block;
  float: left;
  margin-right: 10px;
}
.flag-img {
  width: 22px; /* you can use % */
  margin-left: 10px;
  height: auto;
}

.headshot {
  display: inline-block;
}

.card-body {
  white-space: nowrap;
}

.surfer-info-container {
  display: inline-block;
  text-align: left;
}
.card-title {
  margin: 3px 0px;
}
.loader-container {
  height: 200px;
  position: relative;
}

.center {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.loader {
  margin: auto;
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid #3498db; /* Blue */
  border-radius: 52%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin-top: 20px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
