.profile-container {
  display: flex;
  height: 100vh;
}
#side-bar {
  width: 200px;
  background: #fcfcfc;
  padding-top: 50px;
  border-right: 1px solid #bbc0c4;
}
#profile-content {
  flex: 1;
  /* Grow to rest of container */
}
a {
  color: black;
}

.sss-profile {
  text-align: left;
  padding: 0px 0px 20px 40px;
}

.add-btn {
  margin-top: 32px;
}

.loader {
  margin: auto;
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid #3498db; /* Blue */
  border-radius: 52%;
  width: 30px;
  height: 30px;
  animation: spin 1s linear infinite;
  margin-top: 20px;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
